// http://meyerweb.com/eric/tools/css/reset/
// v2.0 | 20110126

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline; }

// HTML5 display-role reset for older browsers
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block; }

body {
	line-height: 1; }

ol, ul {
	list-style: none; }

blockquote, q {
	quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none; }

table {
	border-collapse: collapse;
	border-spacing: 0; }
///////////////
// Tab Layout
///////////////
body {
	background-color: #e9ebee;
	margin-left: auto;
	margin-right: auto; }

h1 {
  display: flex;
  margin-top: 50px;
  justify-content: center;
  font-size: 20px;
  font-weight: bold; }

.wrap {
  display: flex;
  margin-top: 40px;
  height: 100vh; }

.tab-interface {
  width: 844px;
  margin: 0 auto; }

.tab-list {
	padding: 0;
	margin: 0;
	&::after {
    	display: block;
    	clear: both;
    	content: ""; }
	li {
		float: left {
    	list-style: none; }
		cursor: pointer; }
	li {
	    display: block; } }


[aria-selected="true"] {
	color: #51bcd8 !important;
	border-bottom: 3px solid #51bcd8 !important; }

.unvisual {
	display: none; }


.tab-contents {
	margin-top: 25px; }

.card-bookmark-writing {
	text-decoration: none;
	width: 844px;
	height: 140px;
	box-sizing: border-box;
	display: block;
	margin-bottom: 10px;
	border-radius: 2px;
	background-color: #fff;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.24), 0 2px 0 0 rgba(0, 0, 0, 0.12);
	&:focus {
		box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12);
		outline: none; }
	&:hover {
		box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.24), 0 0 8px 0 rgba(0, 0, 0, 0.12);
		outline: none; } }




.bookmark-section1 {
	height: 140px;
	width: 136px;
	float: left;
	border-right: 1px solid #bdbdbd; }

.bookmark-section2 {
	width: 706px;
	height: 140px;
	position: relative;
	float: left;
	box-sizing: border-box;
	padding: 18px 28px 18px 26px;
	&::after {
		display: block;
		content: "";
		clear: both; } }

.profile-img {
	display: block;
	border-radius: 100%;
	width: 68px;
	height: 68px;
	background-color: #424242;
	margin: {
		left: 34px;
		right: 34px;
		top: 22px; } }

.username {
	display: block;
	width: 80px;
	font-family: 'NanumMyeongjo', serif;
	font-size: 14px;
	font-weight: bold;
	text-align: center;
	color: #424242;
	margin: {
		top: 16px;
		left: 28px;
		right: 28px; } }

.card-content-heading {
	font-family: 'NanumMyeongjo', serif;
	font-size: 20px;
	font-weight: bold;
	color: #424242;
	display: block;
	margin-bottom: 41px; }


.card-content-text {
	font-family: 'NanumMyeongjo', serif;
	font-size: 14px;
	font-weight: normal;
	color: #424242;
	line-height: 1.5; }


.card-info-date {
	font-family: 'NanumMyeongjo', serif;
	font-size: 14px;
	font-weight: bold;
	color: #757575;
	position: absolute;
	display: block;
	top: 18px;
	right: 28px; }
